// Here you can add other styles
.message_box {

    // Tieu de message
    .message-header {
        font-size: 14px;
    }

    // Body message
    .message-body {
        font-size: 15px;

        &.error {
            color: RED;
        }

        &.success {
            color: GREEN;
        }

        &.info {
            color: BLUE;
        }
    }

    // Footer message
    .message-footer {
        font-size: 16px;
    }

}